import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios'; // Import Axios for making HTTP requests

// Import Images
import bnrImg1 from "../../images/banner/img1.jpg";
import waveBlue from "../../images/shap/wave-blue.png";
import circleDots from "../../images/shap/circle-dots.png";
import plusBlue from "../../images/shap/plus-blue.png";

import AppointmentForm from '../elements/appointment-form';

class Booking extends Component {
    // Define a method to handle form submission
    handleSubmit = async (formData) => {
        try {
            const response = await axios.post('http://localhost:3030/booking', formData);
            console.log('Booking successful:', response.data);
            // Handle success, e.g., show a success message
        } catch (error) {
            console.error('Error booking appointment:', error);
            // Handle error, e.g., show an error message
        }
    }

    render() {
        return (
            <>
                <div className="page-content bg-white">
                    <div className="banner-wraper">
                        <div className="page-banner" style={{backgroundImage: `url(${bnrImg1})`}}>
                            <div className="container">
                                <div className="page-banner-entry text-center">
                                    <h1>Booking</h1>
                                    <nav aria-label="breadcrumb" className="breadcrumb-row">
                                        <ul className="breadcrumb">
                                            <li className="breadcrumb-item">
                                                <Link to="/">
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-home">
                                                        <path d="M3 9l9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z"></path>
                                                        <polyline points="9 22 9 12 15 12 15 22"></polyline>
                                                    </svg> Home
                                                </Link>
                                            </li>
                                            <li className="breadcrumb-item active" aria-current="page">Booking</li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                            <img className="pt-img1 animate-wave" src={waveBlue} alt=""/>
                            <img className="pt-img2 animate2" src={circleDots} alt=""/>
                            <img className="pt-img3 animate-rotate" src={plusBlue} alt=""/>
                        </div>
                    </div>
                    <section className="section-area section-sp2 appointment-wraper">
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-xl-5 col-lg-6 col-md-6">
                                    {/* Pass the handleSubmit method as a prop to the AppointmentForm */}
                                    <AppointmentForm onSubmit={this.handleSubmit} />
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </>
        );
    }
}

export default Booking;
